@import url('https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c&family=Montserrat&display=swap');

html{
  overflow: hidden;  
}
body {
  margin: 0;
  font-family: Montserrat,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.sidePanel{

  position: absolute;
  width: 280px;
  height: 720px;
  left: -240px;
  background-color: rgb(30, 30, 44);
  border-radius: 5px;
  box-shadow: 0px 2px 12px rgb(1, 0, 10);
  transition:  500ms;
  padding-top: 20px;
  box-sizing: border-box;
}
.sidePanel:hover{
  left:-20px;
}
.right{  
  left: auto;
  right: -260px;
}
.right:hover{
  left: auto;
  right: -20px;  
}
.sidePanelArrow{
  height: 100%;
  width: 20px;
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
}
.unityWindow{
  border-radius: 5px;
  height: 595px;
  width: 955px;
  box-shadow: 0 0 22px purple;
}
.infoDiv{
  height: 60px;
  width: 80%;
  margin: 20px;
}
.panelText{
  font-size: 16px;
}